.leftsidenav-main {
  background: rgb(33, 32, 30);
  width: 270px;
  padding: 24px 22px;
}
.leftsidenav-main .leftside-nav-fixed {
  position: fixed;
}
.leftsidenav-main .LeftNavLink {
  cursor: pointer;
  display: flex;
  color: #fff;
  gap: 14px;
  align-items: center;
  margin-left: 10px;
  margin-bottom: 40px;
}
.leftsidenav-main .LeftNavLink h3 {
  transition: 0.3s;
  width: 100%;
}
.leftsidenav-main .LeftNavLink h3:hover {
  color: #fff;
}
.leftsidenav-main .leftside-logo {
  margin-bottom: 60px;
}
.leftsidenav-main .leftside-category-name {
  margin: 30px 0px;
  margin-left: 10px;
  letter-spacing: 3px;
}/*# sourceMappingURL=LeftsideNav.css.map */