main {
  width: 100%;
  background: rgb(25, 24, 23);
  h1,
  input {
    display: inline-block;
  }
  input {
    width: 400px;
    height: 45px;
    transform: translateY(-5px);
    border: 1px solid white;
    padding: 0px 20px;
    border-radius: 5px;
    background-color: transparent;
    color: #fff;
    font-size: 14px;
    outline: none;
  }
  h1 {
    font-size: 35px;
  }
  input::placeholder {
    color: #e2e0e0;
  }
}
header {
  width: 100%;
  background-image: url("../../assets/images/Adam.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 720px;
  position: relative;
  overflow-y: hidden;

  .blur {
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
  }
  .blur2 {
    width: 100%;
    height: 100%;
  }
}
nav {
  background: transparent;
  padding: 32px;
  position: relative;
  z-index: 1;
  width: 100%;
  ul {
    display: flex;
    gap: 20px;
    li {
      list-style-type: none;
      a {
        text-decoration: none;
      }
    }
  }
}

.blur {
  position: absolute;
  left: 0px;
  top: 0px;
}
.header-text {
  position: relative;
  padding: 100px;
  h1 {
    width: 500px;
    line-height: 60px;
  }
  .flex {
    display: flex;
    p {
      width: 82px;
      height: 26px;
      box-sizing: border-box;
      background: linear-gradient(
        45deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 0%
      );
      backdrop-filter: blur(11px);
      border-radius: 30px;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 18px 8px;
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    }
  }
  button {
    width: 170px;
    height: 48px;
    background: rgb(248, 36, 47);
    border-radius: 40px;
    border: 0px;
    color: #fff;
    display: flex;
    letter-spacing: 0.1em;
    justify-content: center;
    align-items: center;
    padding: 12px 40px 12px 40px;
    margin-top: 10px;
    cursor: pointer;
  }
}

.allMovie {
  display: flex;
  flex-wrap: wrap;

  padding: 20px 20px 150px 20px;
  .movie {
    width: 23%;
    margin: auto;
    margin-bottom: 30px;
    position: relative;
    height: 250px;
    animation: 2s movie;

    img {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0px;
      top: 0px;
    }
  }
}
.allMovie {
  .movie-info {
    opacity: 0;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    position: absolute;
    width: 100%;
    background-color: #050505;
    left: 30px;
    top: -10px;
    z-index: 1;
    padding: 20px;
    transition: 0.4s;
  }
  .movie:hover {
    .movie-info {
      opacity: 1;
      left: 0px;
    }
  }

  .movie-text {
    button {
      width: 100%;
      height: 30px;
      background-color: transparent;
      color: #fff;
      border: 1px solid grey;
      outline: none;
      cursor: pointer;
      margin-bottom: 10px;
      p {
        color: #fff;
      }
    }
    h2:nth-child(1) {
      color: #fff;
      text-align: center;
      padding-bottom: 20px;
      border-bottom: 2px grey solid;
      margin-bottom: 20px;
      margin-top: 0px;
    }
    h2 {
      margin-top: 20px;

      color: grey;
      margin-bottom: 10px;
    }
    p {
      color: #7f7f7f;
    }
  }
}

@keyframes movie {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
